<template>
  <v-container>
    <v-layout :class="'project ${project.status}'" row>
      <!--CARD 1-->
      <v-card
        v-for="project in projects"
        :key="project.title"
        max-width="300"
        class="mx-auto mb-4"
      >
        <v-list-item>
          <v-list-item-avatar color="grey"></v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title class="headline">
              {{ project.name }}</v-list-item-title
            >
            <v-list-item-subtitle>
              {{ project.items }} lost at {{ project.date }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-img
          src="https://cdn.vuetifyjs.com/images/cards/mountain.jpg"
          height="194"
        ></v-img>

        <v-card-text>
          <p>Description: {{ project.description }}</p>
          <p>Place: {{ project.place }}</p>
        </v-card-text>

        <v-card-actions>
          <v-btn text color="deep-purple accent-4">Read</v-btn>
          <v-btn text color="deep-purple accent-4">
            Bookmark
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn icon>
            <v-icon>mdi-heart</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-share-variant</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script type="text/javascript">
import db from "@/fb.js";

export default {
  data() {
    return {
      projects: []
    };
  }, //colse data

  created() {
    db.collection("projects").onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === "added") {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          });
        } //close if
      });
    }); //close onSnapshot
  } //close created
};
</script>
