<template>
  <v-container>
    <v-layout row>
      <!--CARD 1     -->
      <v-card
        v-for="project in projects"
        :key="project.title"
        width="300"
        class="mx-auto mb-4"
        id="animalContainer"
        v-show="project.items == 'Other'"
      >
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="headline">
              {{ project.name }}</v-list-item-title
            >
            <v-list-item-subtitle>
              {{ project.items }} lost at {{ project.date }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-carousel
          cycle
          height="194"
          width="286"
          hide-delimiter-background
          show-arrows-on-hover
        >
          <v-carousel-item>
            <v-img contain :src="project.firstBase64" height="180"></v-img>
          </v-carousel-item>
          <v-carousel-item>
            <v-img contain :src="project.secondBase64" height="180"></v-img>
          </v-carousel-item>
        </v-carousel>

        <v-card-text>
          <p>Description: {{ project.description }}</p>
          <p>Place: {{ project.place }}</p>
          <p>Medical: {{ project.medical }}</p>
          <p>Contact: {{ project.email }}</p>
        </v-card-text>

        <v-card-actions>
          <v-btn text color="deep-purple accent-4">Read</v-btn>
          <v-btn text color="deep-purple accent-4">
            Bookmark
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn icon>
            <v-icon>mdi-heart</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-share-variant</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script type="text/javascript">
import db from "@/fb.js";
//import othersCarrousel from "../components/othersCarrousel";

export default {
  data() {
    return {
      //slides: ["project.firstBase64", "project.secondBase64"],
      all: [
        "project.name",
        "project.items",
        "project.description",
        "project.place",
        "project.firstBase64",
        "project.secondBase64",
        "project.date",
        "project.medical",
        "project.email"
      ],
      overlay: false,
      zIndex: 1,
      projects: [],
      sizeBox: false
    };
  }, //colse data
  //components:{ othersCarrousel },

  created() {
    db.collection("projects").onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === "added") {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          });
        } //close if
      });
    }); //close onSnapshot
  } //close created
};
</script>
