<template>
  <v-container class="pa-1 ma-0">
    <h2>Form found animals</h2>
    <v-card class="mx-auto mt-5 mb-5" max-width="700">
      <v-form v-model="valid" class="ma-5 pa-2" ref="form">
        <!-- NOMBRE -->
        <v-text-field
          label="Unknown name"
          v-model="name"
          :clearable="clearable"
          placeholder="?"
          prepend-icon="pets"
          hint="Enter your friend's name"
          :max="20"
          :min="2"
          disabled
          :rules="nameRules"
        ></v-text-field>
        <!-- SPEICE -->
        <v-select
          v-model="select"
          :items="items"
          label="Speice"
          prepend-icon="fas fa-dna"
          required
        ></v-select>
        <!-- DESCRIPTION -->
        <v-textarea
          label="Description"
          v-model="description"
          :clearable="clearable"
          placeholder="Color, size, breed, age, sex, hair or any special peculiarities"
          hint="Color, size, breed, age, sex, hair or any special peculiarities"
          prepend-icon="edit"
          :rules="nameRules"
          required
        ></v-textarea>
        <!--CHIP NUMBER-->
        <!-- <v-text-field label="Chip number (9 or 15 digits)" v-model="chip" :clearable="clearable" placeholder="Example: 921500000021217" hint="The number of digits may vary depending on the country" prepend-icon="memory" :rules="chipRules"></v-text-field> -->
        <!-- PLACE -->
        <v-textarea
          label="Place"
          v-model="place"
          :clearable="clearable"
          placeholder="Last place seen"
          hint="Last place seen"
          prepend-icon="location_on"
          :rules="nameRules"
          required
        ></v-textarea>
        <!-- PHOTO 1 -->
        <input
          type="file"
          ref="picture1"
          id="photo1"
          v-on:change="selectFile1"
          :rules="ImageRules"
          accept="image/png, image/jpeg"
          :clearable="clearable"
          :show-size="showSize"
          placeholder="Just .jpeg and .png files, max 2 megas each"
          hint="Just .jpeg and .png files, max 2 megas each"
          prepend-icon="mdi-camera"
          label="Photo1"
          required
        />

        <!--PHOTO 2-->
        <input
          type="file"
          ref="picture2"
          id="photo2"
          v-on:change="selectFile2"
          :rules="ImageRules"
          accept="image/png, image/jpeg"
          :clearable="clearable"
          :show-size="showSize"
          placeholder="Just .jpeg and .png files, max 2 megas each"
          hint="Just .jpeg and .png files, max 2 megas each"
          prepend-icon="mdi-camera"
          label="Photo2"
          required
        />

        <!-- DATE -->
        <v-menu>
          <template v-slot:activator="{ on }">
            <v-text-field
              :value="due"
              v-on="on"
              label="Date"
              prepend-icon="date_range"
              required
            ></v-text-field>
          </template>
          <v-date-picker v-model="due"></v-date-picker>
        </v-menu>
        <!--MEDICAL-->
        <v-textarea
          label="Any medical details?"
          v-model="medical"
          :clearable="clearable"
          placeholder="Your friend's health status. Is your friend taking any medicine or undergoing medical treatment? Any chip number?"
          hint="Your friend's health status. Is your friend taking any medicine or undergoing medical treatment? Any chip number?"
          prepend-icon="healing"
        ></v-textarea>
        <!--MAIL-->
        <v-text-field
          v-model="email"
          :rules="emailRules"
          label="E-mail"
          required
          prepend-icon="email"
        ></v-text-field>
        <!-- SUBMIT -->
        <v-btn @click="submit" :loading="loading" :disabled="disabled"
          >Send</v-btn
        >
        <v-snackbar v-model="snackbar" color="succes" :timeout="0">
          <span>Your report has been succesfully submited</span>
          <v-btn v-on:click="snackbar = false">Close</v-btn>
        </v-snackbar>
        <!--RESET BUTTON-->
        <!-- <v-btn color="error" class="mr-4" @click="resetForm">Reset</v-btn> -->
        <input
          style="margin-left: 15px"
          type="reset"
          onclick="resetForm()"
          id="resetValues"
          value="RESET"
        />
      </v-form>
    </v-card>

    <!--BUTTON-->
    <router-link :to="{ name: 'found-animals' }">
      <v-fab-transition>
        <v-btn color="green" fab large dark bottom class="v-btn--bottom">
          <v-icon>keyboard_arrow_left</v-icon>
        </v-btn>
      </v-fab-transition>
    </router-link>
  </v-container>
</template>

<style type="text/css">
.v-btn--bottom {
  bottom: 0;
  position: fixed;
  margin-bottom: 50px;
  margin-right: 15px;
  right: 0;
}
</style>
<script type="text/javascript">
import db from "@/fb.js";

export default {
  methods: {
    selectFile1(event) {
      //validate size
      var imgSize1 = document.getElementById("photo1").files[0];
      if (imgSize1.size > 500000) {
        console.log(imgSize1.size);
        console.log(imgSize1.length);
        alert("picture must be less that 0.5MG and .jpeg or .png extention");
        document.getElementById("photo1").value = "";
        return false;
      } //validate
      //const file = this.$refs.file.files;
      const files = event.target.files;
      let filename = files[0].name;
      if (filename.lastIndexOf(".") <= 0) {
        return alert("please select a valid file");
      } //if
      const fileReader = new FileReader();
      fileReader.addEventListener("load", () => {
        this.imageURL1 = fileReader.result;
      }); // Convert the file to base64 text
      fileReader.readAsDataURL(files[0]);
      this.image = files[0];
    }, //select1
    selectFile2(event) {
      //validate size
      var imgSize2 = document.getElementById("photo2").files[0];
      if (imgSize2.size > 500000) {
        alert("picture must be less that 0.5MG and .jpeg or .png extention");
        document.getElementById("photo2").value = "";
        console.log(imgSize2.size);
        return false;
      } //validate
      const files = event.target.files;
      let filename = files[0].name;
      if (filename.lastIndexOf(".") <= 0) {
        return alert("please select a valid file");
      } //if
      const fileReader = new FileReader();
      fileReader.addEventListener("load", () => {
        this.imageURL2 = fileReader.result;
      }); // Convert the file to base64 text
      fileReader.readAsDataURL(files[0]);
      this.image = files[0];
    }, //select2

    resetForm() {
      this.$refs.form.reset();
      this.loading = false;
    }, //reset
    submit: function() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        const project = {
          name: this.name,
          items: this.select,
          description: this.description,
          // chip: this.chip,
          place: this.place,
          //photo: this.file,
          firstBase64: this.imageURL1,
          secondBase64: this.imageURL2,
          date: this.due,
          medical: this.medical,
          email: this.email
        }; //project
        db.collection("projects0")
          .add(project)
          .then(() => {
            console.log("added to db");
            this.loading = false;
            this.disabled = true;
            this.snackbar = true;
            this.$refs.form.reset();
          }); //then
      } //if
    } //submit
  }, //methods
  //name: "FormLostAnimal",
  data() {
    return {
      valid: true,
      name: "Unknown name",
      select: null,
      items: ["Cat", "Dog", "Other"],
      description: "",
      place: "",
      due: null,
      medical: "",
      email: "",
      image: null,
      imageURL1: "",
      imageURL2: "",
      chip: "",
      //text atributes
      clearable: true,
      disabled: false,
      counter: 0,
      showSize: true,
      snackbar: false,
      ImageRules: [
        value =>
          !value ||
          value.size < 500000 ||
          "Photo size should be less than 2 MB!"
      ],
      emailRules: [
        value => value == "" || !!value || "E-mail is required",
        value =>
          value == "" || /.+@.+\..+/.test(value) || "E-mail must be valid"
      ],
      nameRules: [
        value => value == null || value.length >= 3 || "Minimum 3 characters"
      ],
      chipRules: [
        value =>
          value == null ||
          value.length == 9 ||
          value.length == 15 ||
          "9 or 15 digits"
      ],
      loading: false
    };
  } //data
}; //export
</script>
