<script type="text/javascript">
import db from "@/fb.js";

export default {
  data() {
    return {
      slides: ["project.firstBase64", "project.secondBase64"],
      items: [{ src: "project.firstBase64" }, { src: "project.secondBase64" }],
      // items: [
      // {src: require('project.firstBase64')},
      // {src: require('project.secondBase64')}
      // ],
      projects: []
    };
  }, //colse data

  created() {
    db.collection("projects").onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === "added") {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          });
        } //close if
      });
    }); //close onSnapshot
  } //close created
};
</script>
