<template>
  <v-container>
    <v-layout row>
      <!--CARD 1-->
      <v-card
        v-for="project in projects"
        :key="project.title"
        width="300"
        class="mx-auto mb-4 animalCont"
        id="animalContainer"
        v-show="project.items == 'Report-abuse'"
      >
        <v-list-item>
          <!--  <v-list-item-avatar color="grey"></v-list-item-avatar> -->
          <v-list-item-content>
            <v-list-item-title class="headline">
              {{ project.title }}</v-list-item-title
            >
            <v-list-item-subtitle>
              {{ project.items }} post at {{ project.date }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-carousel
          :class="{ bigCarousel: bigCarousel }"
          cycle
          height="194"
          width="286"
          hide-delimiter-background
          show-arrows-on-hover
        >
          <v-carousel-item>
            <v-img contain :src="project.firstBase64" height="180"></v-img>
          </v-carousel-item>
          <v-carousel-item>
            <v-img contain :src="project.secondBase64" height="180"></v-img>
          </v-carousel-item>
        </v-carousel>

        <v-card-text>
          <p>Description: {{ project.description }}</p>
          <p>Place: {{ project.place }}</p>
          <p>Name: {{ project.name }}</p>
          <p>Contact: {{ project.email }}</p>
        </v-card-text>

        <v-card-actions>
          <v-btn
            @click="bigCarousel = !bigCarousel"
            v-model="bigCarousel"
            text
            color="deep-purple accent-4"
            >Read</v-btn
          >

          <v-btn text color="deep-purple accent-4">
            Bookmark
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn icon>
            <v-icon>mdi-heart</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-share-variant</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<style type="text/css">
.bigCarousel {
  height: 500px;
  width: 500px;
}
</style>

<script type="text/javascript">
import db from "@/fb.js";

export default {
  data() {
    return {
      slides: ["project.firstBase64", "project.secondBase64"],
      all: [
        "project.title",
        "project.name",
        "project.items",
        "project.description",
        "project.place",
        "project.firstBase64",
        "project.secondBase64",
        "project.date",
        "project.email"
      ],
      active: false,
      bigCarousel: false,
      projects: []
    };
  }, //colse data
  mounted: function() {
    this.catTag = function() {
      var x = document.getElementById("cardItem");
      return console.log(x);
    };
  }, //mounted

  created() {
    db.collection("projects1").onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === "added") {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          });
        } //close if
      });
    }); //close onSnapshot
  } //close created
};
</script>
